@import "https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;400&family=Montserrat&display=swap" ;

*{margin: 0; padding: 0; box-sizing: border-box;}

.main{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 30px;
  border-bottom: 1px solid rgba(196, 196, 196, 0.4);
  margin: 0 30px;
}
.child_1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.item_quantity {
  width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 18px;
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    color: #3c3c3c;
    background-color: rgba(196, 196, 196, 0.19);
    border-radius: 2px;
}
.child_2 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 20px;
}
.btns{
  display: flex;
  justify-content: flex-start;

}
.more{
  margin-left: 15px;
}
.i{
  margin-top:3px;
  margin-bottom:3px;
}
.item_name{
  font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 16px;
color: #000000;

}
.item_price{
  font-family: 'NATS';
  font-size: 24px;
  font-weight: 400;
  color:rgba(0, 0, 0, 1);
  line-height: 16px;
}
.item_color{
  font-family: 'Montserrat', sans-serif;
  font-size: 13px;
  font-weight: bold;
  color:rgba(0, 0, 0, 0.7);
  line-height: 16px;
}
.clr{
  font-weight: lighter;
}
.item_avail{
  font-family: 'Montserrat';
  font-size: 13px;
  font-weight: 400;
  color:rgba(0, 156, 44, 0.81);
  line-height: 16px;
}
.del{
  border: 2px solid #F77000;
border-radius: 15px;
background-color: #fff;
width: 73px;
height: 24px;
font-family: 'NATS';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 16px;
margin-top: 5px;
cursor: pointer;
}
.more{
  background: #F77000;
border-radius: 15px;
font-family: 'NATS';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 16px;
width: 99px;
height: 24px;
color: #FFFFFF;
border: none;
margin-top: 5px;
cursor: pointer;

}
.proceed{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* position: fixed;
  bottom: 30px;
  width: 100%; */
}
h3.total{
  font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 14px;
line-height: 40px;
text-align: center;
color: #000000;
}
.pro_btn{
 width: 255px;
height: 44px;
background: #DB4700;
box-shadow: -3px -6px 4px rgba(220, 220, 220, 0.4), 3px 6px 4px rgba(196, 196, 196, 0.4);
border-radius: 15px;
font-family: 'NATS';
font-style: normal;
font-weight: 400;
font-size: 18px;
line-height: 40px;
text-align: center;
color: #FFFFFF;
border: none;
cursor: pointer;
}
.search{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center; 
  max-width:700px ;
  min-width: 370px;
  height: 28px;  margin: 10px 0;
  background: rgba(196, 196, 196, 0.19);
border-radius: 5px;
}
input{
  background-color:transparent;
  border: none;
  padding: 5px 10px 5px 44px;


  font-size: 16px;
  display: block;
  width: 100%;
  outline: 0;
}
::placeholder{
font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 13px;
line-height: 16px;
color: rgba(71, 71, 71, 0.6);
}
.smain{
  display: flex;
  justify-content: center;
  align-items: center;
}
.lv{
  display: flex;
  justify-content:space-between;
  align-items: center;
}
.vect,.log{
  padding: 5px 15px;
}
